import React from 'react';
import { Card } from 'react-bootstrap';

const TilesCards = () => {
    return (
        <React.Fragment>
            <div className="row tiles-card">
                {TilesData?.map((list, index) => {
                    return (
                        <div className="col-xl-3 col-lg-6 col-md-6 col-sm-12 col-12" key={index}>
                            <Card>
                                <Card.Body>
                                    <h4>{list?.title}</h4>
                                    <h2>{list?.count}</h2>
                                </Card.Body>
                            </Card>
                        </div>
                    );
                })}

            </div>
        </React.Fragment>
    );
}

export default TilesCards;

const TilesData = [
    {
        title: "Today Appointment",
        count: "125",
    },
    {
        title: "Patient List",
        count: "2005",
    },
    {
        title: "Mode Tracker",
        count: "63",
    },
    {
        title: "Notes Patient",
        count: "51",
    },
];
