import React, { useState } from "react";
import FullCalendar from '@fullcalendar/react'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import { Modal } from "react-bootstrap";

export default function Calendar() {
    const [detailModalOpen, setDetailModalOpen] = useState(false);
    const [selectedEventInfo, setSelectedEventInfo] = useState(null);

    const headerToolbar = {
        left: 'prev,next today',
        center: 'title',
        right: 'dayGridMonth'
    };
    const onEventClick = (eventInfo) => {
        setSelectedEventInfo(eventInfo.event);
        setDetailModalOpen(true);
    };
    const renderEventContent = (event) => {
        return (
            <div className="appointment-event">
                <p className="font-100 mb-0">
                    5:00 PM
                </p>
                <p className="mb-0 line-height-1">-</p>
                <p className="font-100 mb-0">
                    5:30 PM
                </p>

            </div>
        );
    };

    return (
        <div className="appointment-calender">
            <FullCalendar
                plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
                headerToolbar={headerToolbar}
                eventContent={renderEventContent}
                // events={calendarEvents}
                dayMaxEventRows={2}
                dayMaxEvent={true}
                eventClick={onEventClick}
                events={[
                    { title: 'event 1', date: '2019-04-01' },
                    { title: 'event 2', date: '2019-04-02' }
                ]}
            />
            <AppointmentSlotInfo
                show={detailModalOpen}
                handleClose={() => setDetailModalOpen(false)}
            // selectedEventInfo={selectedEventInfo}
            />
        </div>
    )
}
export function AppointmentSlotInfo(props) {
    return (
        <React.Fragment>
            <Modal
                show={props.show}
                onHide={props.handleClose}
                centered
                dialogClassName="add-modal-width">

                {/* <Modal.Header className="h5" closeButton>APPOINTMENT DETAILS</Modal.Header> */}
                <Modal.Body>
                    <div className="card border-0">
                        <div className="card-body border-0 pt-2">

                            <div className="row">
                                <div className="col-11">
                                    <h2 className="craate-appointment-slot-heading">{"Appointment Detail"}</h2>
                                </div>
                                <div className="col-1">
                                    {/* <div className="col-md-1" ><MdClose className="d-flex justify-content-end dr-profile-close-icon" onClick={props.handleClose} /></div> */}
                                </div>
                            </div>
                            <div className="row justify-content-center">
                                <div className="col-6">
                                    <p className="dr-profile-qualification-text">
                                        {"Appointment Date & Time"}
                                    </p>
                                </div>
                                <div className="col-6 mt-3">
                                    <h4 className="dr-profile-speciaties-list d-flex justify-content-center align-items-center">
                                        2022-07-25
                                        <small className="ml-2">
                                            5:00 PM-5:30 PM
                                        </small>
                                    </h4>
                                </div>
                            </div>
                        </div>
                    </div>
                </Modal.Body>
            </Modal>
        </React.Fragment >
    )
}