import React from "react";
import { IMAGES } from "../../../assets/images";

export default function UpcomingAppointments() {
    return (
        <React.Fragment>
            <div className="upcoming-appointments">
                <h3>Upcoming Appointments</h3>
                {AppointmentsList?.map((list, index) => {
                    return (
                        <div className="d-flex align-items-center justify-content-between py-3 border-bottom" key={index}>
                            <div className="d-flex align-items-center justify-content-center">
                                <img src={IMAGES.PROFILELOGO} className="img-fluid" alt="user-imgs" />
                                <div>
                                    <h5>{list?.name}</h5>
                                    <p>{list?.designation}</p>
                                </div>
                            </div>
                            <p className="mt-4">{list?.date}</p>
                            <span>{list?.status}</span>
                        </div>
                    );
                })}

            </div >
        </React.Fragment >
    )
}
